import type { IControl } from 'maplibre-gl';
import { LayerSwitcherControl } from './LayerSwitcherControl';
import type { BaseLayer } from './LayerSwitcherControl';
import { InfoControl } from './controls/InfoControl';
import { ModernZoomControl, ModernResetControl, ModernPrintControl } from './controls/ModernControls';

// Dfinition d'une Factory de contrle
/**
 * Type reprsentant une fonction capable de crer un contrle MapLibre (synchrone ou asynchrone).
 */
export type ControlFactory = (options?: unknown) => IControl | Promise<IControl>;

/**
 * Registre centralisant les plugins et contrles disponibles pour la carte.
 * Suit le pattern **Factory** pour permettre une extension facile.
 */
export class ControlRegistry {
  private factories: Map<string, ControlFactory> = new Map();

  constructor() {
    this.registerDefaults();
  }

  /**
   * Enregistre les contrles natifs et par dfaut de la librairie.
   */
  private registerDefaults() {
    // Zoom / Rotation
    this.register('zoom', () => new ModernZoomControl());
    
    // Print
    this.register('print', () => new ModernPrintControl());

    // Reset / Home
    this.register('reset', () => new ModernResetControl());
    
    // Info / Metadata
    this.register('info', () => new InfoControl());

    // Drawing Tools (ASYNCHRONE : Ne charge MapboxDraw que si utilis)
    if (import.meta.env.VITE_WITH_DRAW !== 'false') {
      this.register('draw', async (opts) => {
        const { DrawingControl } = await import('./controls/DrawingControl');
        return new DrawingControl(opts);
      });
    }

    // Layer Switcher (avec configuration par dfaut)
    this.register('layers', () => {
       const baseLayers: BaseLayer[] = [
          { name: 'OpenStreetMap', url: 'https://tile.openstreetmap.org/{z}/{x}/{y}.png' },
          { name: 'CartoDB Dark', url: 'https://basemaps.cartocdn.com/dark_all/{z}/{x}/{y}.png' },
          { name: 'CartoDB Light', url: 'https://basemaps.cartocdn.com/light_all/{z}/{x}/{y}.png' }
        ];
        // Note: Le callback onChange sera inject/gr par la map si besoin, 
        // mais ici LayerSwitcherControl attend le callback dans le constructeur.
        // Pour rester gnrique, on va modifier lgrement LayerSwitcherControl ou l'adapter ici.
        // Pour ce POC, nous allons ruser : on retourne une instance partiellement configure
        // et la map liera le callback aprs.
        // Mais pour faire simple, on passe une fonction vide qui sera remplace.
        return new LayerSwitcherControl(baseLayers, () => {}); 
    });
  }

  public register(name: string, factory: ControlFactory) {
    this.factories.set(name, factory);
  }

  /**
   * Cre une instance de contrle  partir de son nom.
   * 
   * @param name - Nom du contrle  instancier.
   * @param options - Options de configuration pour le contrle.
   * @returns L'instance IControl (ou une Promise) ou null si non trouv.
   */
  public create(name: string, options?: unknown): IControl | Promise<IControl> | null {
    const factory = this.factories.get(name);
    if (!factory) {
      console.warn(`Control '${name}' not found in registry.`);
      return null;
    }
    return factory(options);
  }
}

export const globalControlRegistry = new ControlRegistry();
