import type { Map, MapMouseEvent, MapGeoJSONFeature, LngLatLike } from 'maplibre-gl';

export type InteractionEvent = {
  type: 'click' | 'hover';
  feature: MapGeoJSONFeature;
  originalEvent: Event;
  lngLat?: LngLatLike;
};

export type InteractionCallback = (event: InteractionEvent) => void;

/**
 * Gre les interactions utilisateur (clic, survol) sur les couches vectorielles.
 */
export class InteractionManager {
  private map: Map;
  private callback: InteractionCallback;
  // Liste des prfixes de calques interactifs (lis au Double Buffering)
  private interactiveLayers = ['data-fill', 'data-line', 'data-circle'];

  constructor(map: Map, callback: InteractionCallback) {
    this.map = map;
    this.callback = callback;
    this.initListeners();
  }

  private initListeners() {
    this.map.on('click', this.handleClick.bind(this));
    this.map.on('mousemove', this.handleHover.bind(this));
    this.map.on('mouseleave', this.handleLeave.bind(this));
  }

  private getInteractiveLayerIds(): string[] {
    // On doit cibler les calques actifs ET inactifs (0 et 1) car queryRenderedFeatures
    // peut retourner des lments du buffer cach si on ne filtre pas bien, 
    // mais surtout, on veut interagir avec ce qui est visible.
    // MapLibre gre la visibilit, donc on peut lister tous les IDs potentiels.
    const ids: string[] = [];
    this.interactiveLayers.forEach(prefix => {
      ids.push(`${prefix}-0`, `${prefix}-1`);
    });
    return ids;
  }

  private hasProperties(feature: MapGeoJSONFeature): boolean {
    const hasProps = feature.properties && Object.keys(feature.properties).length > 0;
    if (!hasProps) {
        // Debug log to confirm why we are ignoring this feature
        // console.debug('InteractionManager: Ignoring feature with no properties', feature);
    }
    return !!hasProps;
  }

  private handleClick(e: MapMouseEvent) {
    const features = this.map.queryRenderedFeatures(e.point, {
      layers: this.getInteractiveLayerIds()
    });

    if (features.length > 0) {
      const feature = features[0];
      // Ignore features with no info
      if (this.hasProperties(feature)) {
        this.callback({
          type: 'click',
          feature: feature,
          originalEvent: e.originalEvent,
          lngLat: e.lngLat
        });
      }
    }
  }

  private handleHover(e: MapMouseEvent) {
    const features = this.map.queryRenderedFeatures(e.point, {
      layers: this.getInteractiveLayerIds()
    });

    if (features.length > 0 && this.hasProperties(features[0])) {
      this.map.getCanvas().style.cursor = 'pointer';
    } else {
      this.map.getCanvas().style.cursor = '';
    }
  }

  private handleLeave() {
    this.map.getCanvas().style.cursor = '';
  }

  public destroy() {
    this.map.off('click', this.handleClick.bind(this));
    this.map.off('mousemove', this.handleHover.bind(this));
    this.map.off('mouseleave', this.handleLeave.bind(this));
  }
}
