import { globalPerformanceCollector } from './PerformanceCollector';
// @ts-ignore - Vite handled worker inlining
import GeoJSONWorker from '../workers/geojson-worker?worker&inline';

export type ProgressCallback = (bytes: number, features: number) => void;
export type BatchCallback = (bounds: number[][]) => void;

/**
 * Service de haut niveau grant le cycle de vie du Web Worker et la communication des tuiles.
 */
export class GeoJSONLoader {
  private worker: Worker | null = null;
  private tileRequests: Map<string, (data: Uint8Array | null) => void> = new Map();
  private requestId = 0;

  public async load(
    url: string, 
    onBatch: BatchCallback,
    onProgress?: ProgressCallback,
    clear: boolean = true
  ): Promise<void> {
    if (clear) this.abort();
    const startTime = performance.now();

    return new Promise((resolve, reject) => {
      if (!this.worker) {
        this.worker = new GeoJSONWorker();
      }

      this.worker.onmessage = (event) => {
        const { type, bytes, features, message, id, tile, bounds, metrics } = event.data;

        // Collecter les mtriques du worker si prsentes
        if (metrics) {
          if (metrics.indexingTime) globalPerformanceCollector.logMetric('Worker Indexing', metrics.indexingTime, 'ms');
          if (metrics.memory) globalPerformanceCollector.logMetric('Worker Memory', metrics.memory, 'MB');
          if (metrics.parsingTime) globalPerformanceCollector.logMetric('Worker Parsing', metrics.parsingTime, 'ms');
        }

        switch (type) {
          case 'progress':
            const elapsed = (performance.now() - startTime) / 1000; // sec
            if (elapsed > 0) {
              const mbps = (bytes / 1024 / 1024) / elapsed;
              globalPerformanceCollector.logMetric('Network Speed', mbps, 'MB/s');
            }
            if (onProgress) onProgress(bytes, features);
            break;
          case 'tile-result':
            if (metrics?.tileGeneration) globalPerformanceCollector.logMetric('Tile Gen (Worker)', metrics.tileGeneration, 'ms');
            const callback = this.tileRequests.get(id);
            if (callback) {
              callback(tile);
              this.tileRequests.delete(id);
            }
            break;
          case 'index-updated':
            onBatch(bounds); 
            break;
          case 'done':
            if (metrics?.totalTime) globalPerformanceCollector.logMetric('Total Load Time', metrics.totalTime, 'ms');
            resolve();
            break;
          case 'error':
            reject(new Error(message));
            break;
        }
      };

      this.worker.postMessage({ type: 'load', url, clear });
    });
  }

  public getTile(z: number, x: number, y: number): Promise<Uint8Array | null> {
    return new Promise((resolve) => {
      if (!this.worker) return resolve(null);
      const id = `req_${this.requestId++}`;
      this.tileRequests.set(id, resolve);
      this.worker.postMessage({ type: 'getTile', z, x, y, id });
    });
  }

  public abort() {
    if (this.worker) {
      this.worker.terminate();
      this.worker = null;
      this.tileRequests.clear();
    }
  }
}