import type { LayerSpecification, FillLayerSpecification, LineLayerSpecification, CircleLayerSpecification } from 'maplibre-gl';

export interface StyleConfig {
  fill?: FillLayerSpecification['paint'];
  line?: LineLayerSpecification['paint'];
  circle?: CircleLayerSpecification['paint'];
}

/**
 * Gre la gnration des calques MapLibre en respectant la stratgie de Double Buffering.
 */
export class LayerManager {
  private config: StyleConfig;

  // Configuration par dfaut (celle hardcode prcdemment)
  private readonly defaults: StyleConfig = {
    fill: { 'fill-color': '#0080ff', 'fill-opacity': 0.4 },
    line: { 'line-color': '#0080ff', 'line-width': 1 },
    circle: { 'circle-color': '#ff0000', 'circle-radius': 5 }
  };

  constructor(userConfig: StyleConfig = {}) {
    this.config = {
      fill: { ...this.defaults.fill, ...userConfig.fill },
      line: { ...this.defaults.line, ...userConfig.line },
      circle: { ...this.defaults.circle, ...userConfig.circle }
    };
  }

  /**
   * Gnre la liste complte des calques pour les deux buffers.
   */
  public generateLayers(): LayerSpecification[] {
    const layers: LayerSpecification[] = [];

    // On gnre pour les deux buffers (0 et 1)
    [0, 1].forEach(bufferIndex => {
      const isVisible = bufferIndex === 0;
      const visibility = isVisible ? 'visible' : 'none';
      const source = `streamed-data-${bufferIndex}`;

      // 1. Polygones (Fill)
      layers.push({
        id: `data-fill-${bufferIndex}`,
        type: 'fill',
        source: source,
        'source-layer': 'streamed-layer',
        filter: ['==', '$type', 'Polygon'],
        paint: this.config.fill,
        layout: { visibility }
      } as FillLayerSpecification);

      // 2. Lignes (Line)
      layers.push({
        id: `data-line-${bufferIndex}`,
        type: 'line',
        source: source,
        'source-layer': 'streamed-layer',
        paint: this.config.line,
        layout: { visibility }
      } as LineLayerSpecification);

      // 3. Points (Circle)
      layers.push({
        id: `data-circle-${bufferIndex}`,
        type: 'circle',
        source: source,
        'source-layer': 'streamed-layer',
        filter: ['==', '$type', 'Point'],
        paint: this.config.circle,
        layout: { visibility }
      } as CircleLayerSpecification);
    });

    return layers;
  }
}
