export interface MetricUpdate {
  name: string;
  value: number;
  unit: string;
  isWarning?: boolean;
}

export type WarningCallback = (message: string) => void;

/**
 * Service de collecte de donnes de performance.
 * Centralise les mtriques sans se soucier du rendu (SOLID).
 */
export class PerformanceCollector {
  private static instance: PerformanceCollector;
  private metrics: Map<string, number> = new Map();
  private units: Map<string, string> = new Map();
  private warnings: Set<string> = new Set();
  private onWarning: WarningCallback | null = null;

  // Seuils de diagnostic
  private readonly THRESHOLDS = {
    INDEXING_TIME: 500, // ms
    PARSING_LATENCY: 200, // ms
    MEMORY_MB: 400 // Mo
  };

  private constructor() {}

  public static getInstance(): PerformanceCollector {
    if (!PerformanceCollector.instance) {
      PerformanceCollector.instance = new PerformanceCollector();
    }
    return PerformanceCollector.instance;
  }

  public setWarningCallback(cb: WarningCallback) {
    this.onWarning = cb;
  }

  public logMetric(name: string, value: number, unit: string) {
    this.metrics.set(name, value);
    this.units.set(name, unit);
    this.checkDiagnostics(name, value);
  }

  private checkDiagnostics(name: string, value: number) {
    let msg = '';
    if (name === 'Worker Indexing' && value > this.THRESHOLDS.INDEXING_TIME) {
      msg = ` Goulot d'tranglement : Indexation lente (${value.toFixed(0)}ms)`;
    } else if (name === 'Worker Memory' && value > this.THRESHOLDS.MEMORY_MB) {
      msg = ` Attention : Consommation mmoire leve (${value.toFixed(0)}MB)`;
    }

    if (msg && !this.warnings.has(msg)) {
      this.warnings.add(msg);
      this.onWarning?.(msg);
    }
  }

  public getMetrics(): MetricUpdate[] {
    const result: MetricUpdate[] = [];
    this.metrics.forEach((value, name) => {
      result.push({
        name,
        value,
        unit: this.units.get(name) || ''
      });
    });
    return result;
  }
}

export const globalPerformanceCollector = PerformanceCollector.getInstance();
