import type { IControl, Map as MapLibreMap, MapMouseEvent } from 'maplibre-gl';

/**
 * Plugin de contrle affichant les mtadonnes des entits GeoJSON lors d'un clic.
 * Gre galement le changement de curseur lors du survol des donnes interactives.
 */
export class InfoControl implements IControl {
  private container: HTMLElement | undefined;
  private map: MapLibreMap | undefined;
  private contentDiv: HTMLElement | undefined;
  
  // Configuration pour savoir quelles couches couter
  // Dans notre POC, c'est harcod aux couches de StreamingMap, mais on pourrait le passer en config
  private interactiveLayers = ['data-circle', 'data-fill', 'data-line'];

  /**
   * Initialisation du contrle.
   */
  onAdd(map: MapLibreMap): HTMLElement {
    this.map = map;
    this.container = document.createElement('div');
    this.container.className = 'maplibregl-ctrl maplibregl-ctrl-group info-control';
    this.container.style.display = 'none'; // Cach par dfaut
    this.container.style.padding = '10px';
    this.container.style.maxWidth = '300px';
    this.container.style.maxHeight = '200px';
    this.container.style.overflowY = 'auto';

    this.contentDiv = document.createElement('div');
    this.container.appendChild(this.contentDiv);

    // coute des vnements
    // On utilise un wrapper pour pouvoir le dsabonner proprement
    this.onMapClick = this.onMapClick.bind(this);
    this.map.on('click', this.interactiveLayers, this.onMapClick);
    
    // Change le curseur
    this.map.on('mouseenter', 'data-circle', () => this.map!.getCanvas().style.cursor = 'pointer');
    this.map.on('mouseleave', 'data-circle', () => this.map!.getCanvas().style.cursor = '');
    this.map.on('mouseenter', 'data-fill', () => this.map!.getCanvas().style.cursor = 'pointer');
    this.map.on('mouseleave', 'data-fill', () => this.map!.getCanvas().style.cursor = '');

    return this.container;
  }

  /**
   * Nettoyage des couteurs et du DOM.
   */
  onRemove(): void {
    if (this.map) {
      this.map.off('click', this.interactiveLayers, this.onMapClick);
    }
    this.container?.parentNode?.removeChild(this.container);
    this.map = undefined;
  }

  private onMapClick(e: MapMouseEvent & { features?: { properties: Record<string, unknown> }[] }) {
    if (!e.features || e.features.length === 0) {
      this.container!.style.display = 'none';
      return;
    }

    const feature = e.features[0];
    const props = feature.properties;

    // Affichage "CRUD" brut des proprits (KISS)
    // On pourrait injecter un template HTML ici via une mthode de config externe
    let html = '<strong>Dtails</strong><hr style="margin:5px 0; border:0; border-top:1px solid #eee;">';
    
    if (Object.keys(props).length === 0) {
      html += '<em>Aucune proprit</em>';
    } else {
      html += '<table style="font-size:0.9em; width:100%;">';
      for (const key in props) {
        html += `<tr>
          <td style="font-weight:bold; padding-right:10px; vertical-align:top;">${key}</td>
          <td style="word-break:break-all;">${props[key]}</td>
        </tr>`;
      }
      html += '</table>';
    }

    this.contentDiv!.innerHTML = html;
    this.container!.style.display = 'block';
  }
}
