import type { IControl, Map } from 'maplibre-gl';

/**
 * Contrle de Zoom personnalis.
 */
export class ModernZoomControl implements IControl {
  onAdd(map: Map): HTMLElement {
    const group = document.createElement('div');
    group.className = 'maplibregl-ctrl-group modern-ctrl-group';
    
    const createBtn = (html: string, title: string, action: () => void) => {
      const btn = document.createElement('button');
      btn.type = 'button';
      btn.title = title;
      btn.innerHTML = html;
      btn.onclick = action;
      return btn;
    };

    group.appendChild(createBtn('+', 'Zoom avant', () => map.zoomIn()));
    group.appendChild(createBtn('-', 'Zoom arrire', () => map.zoomOut()));
    
    return group;
  }
  onRemove() {}
}

/**
 * Contrle de Rinitialisation (Home).
 */
export class ModernResetControl implements IControl {
  onAdd(map: Map): HTMLElement {
    const group = document.createElement('div');
    group.className = 'maplibregl-ctrl-group modern-ctrl-group';
    
    const btn = document.createElement('button');
    btn.type = 'button';
    btn.title = 'Rinitialiser la vue';
    btn.innerHTML = '';
    btn.onclick = () => map.flyTo({ center: [2.35, 48.85], zoom: 5 });
    
    group.appendChild(btn);
    return group;
  }
  onRemove() {}
}

/**
 * Contrle d'Impression.
 */
export class ModernPrintControl implements IControl {
  onAdd(): HTMLElement {
    const group = document.createElement('div');
    group.className = 'maplibregl-ctrl-group modern-ctrl-group';
    
    const btn = document.createElement('button');
    btn.type = 'button';
    btn.title = 'Imprimer la carte';
    btn.innerHTML = '';
    btn.onclick = () => window.print();
    
    group.appendChild(btn);
    return group;
  }
  onRemove() {}
}
