import type { IControl, Map as MapLibreMap } from 'maplibre-gl';

/**
 * Reprsente une dfinition de couche de fond (Raster).
 */
export type BaseLayer = {
  /** Nom affich dans l'interface */
  name: string;
  /** URL au format XYZ (ex: https://.../{z}/{x}/{y}.png) */
  url: string;
};

/**
 * Contrle MapLibre permettant de basculer entre diffrents fonds de carte.
 */
export class LayerSwitcherControl implements IControl {
  private container: HTMLElement | undefined;
  private layers: BaseLayer[];
  private onChange: (layer: BaseLayer) => void;

  constructor(layers: BaseLayer[], onChange: (layer: BaseLayer) => void) {
    this.layers = layers;
    this.onChange = onChange;
  }

  /**
   * Met  jour le callback appel lors du changement de couche.
   */
  public setOnChange(callback: (layer: BaseLayer) => void) {
    this.onChange = callback;
  }

  /**
   * Mthode IControl : Cration de l'lment DOM du contrle.
   */
  onAdd(_map: MapLibreMap): HTMLElement {
    this.container = document.createElement('div');
    this.container.className = 'maplibregl-ctrl maplibregl-ctrl-group layer-switcher';
    
    // Style minimaliste inject dynamiquement pour rester KISS
    this.container.style.padding = '10px';
    this.container.style.fontFamily = 'sans-serif';
    this.container.style.fontSize = '12px';

    this.layers.forEach((layer, index) => {
      const wrapper = document.createElement('div');
      wrapper.style.marginBottom = '5px';
      
      const input = document.createElement('input');
      input.type = 'radio';
      input.name = 'baselayer';
      input.id = `layer-${index}`;
      input.checked = index === 0;
      input.style.cursor = 'pointer';
      
      const label = document.createElement('label');
      label.htmlFor = `layer-${index}`;
      label.textContent = layer.name;
      label.style.marginLeft = '5px';
      label.style.cursor = 'pointer';

      input.addEventListener('change', () => {
        if (input.checked) {
          this.onChange(layer);
        }
      });

      wrapper.appendChild(input);
      wrapper.appendChild(label);
      this.container!.appendChild(wrapper);
    });

    return this.container;
  }

  onRemove(): void {
    this.container?.parentNode?.removeChild(this.container);
  }
}
