:root {
  font-family: Inter, system-ui, Avenir, Helvetica, Arial, sans-serif;
  line-height: 1.5;
  font-weight: 400;
}

body {
  margin: 0;
  display: flex;
  place-items: center;
  min-width: 320px;
  min-height: 100vh;
}

#app {
  width: 100%;
  height: 100vh;
  margin: 0;
  padding: 0;
}

.container {
  display: flex;
  flex-direction: column;
  height: 100%;
}

.controls {
  padding: 1rem;
  background: #f0f0f0;
  border-bottom: 1px solid #ddd;
  z-index: 10;
}

h1 { margin: 0; font-size: 1.2rem; }
p { margin: 0; font-size: 0.9rem; color: #666; }

streaming-map {
  flex-grow: 1;
  width: 100%;
}