import maplibregl from 'maplibre-gl';
import type { RequestParameters } from 'maplibre-gl';
import mapLibreCss from 'maplibre-gl/dist/maplibre-gl.css?inline';

// Import conditionnel du CSS de Draw (pour viter de l'inclure dans le bundle lite)
const mapboxDrawCss = import.meta.env.VITE_WITH_DRAW !== 'false' 
  ? (await import('@mapbox/mapbox-gl-draw/dist/mapbox-gl-draw.css?inline')).default 
  : '';

import { globalControlRegistry } from './ControlRegistry';
import type { ControlFactory } from './ControlRegistry';
import { PerformanceMonitor } from './utils/PerformanceMonitor';
import { globalPerformanceCollector } from './utils/PerformanceCollector';
import { GeoJSONLoader } from './utils/GeoJSONLoader';
import { LayerManager, type StyleConfig } from './utils/LayerManager';
import { InteractionManager, type InteractionEvent } from './utils/InteractionManager';
import { PopupManager } from './utils/PopupManager';

/**
 * Composant Web `<streaming-map>` permettant d'afficher une carte MapLibre
 * avec un chargement progressif des donnes GeoJSON via tuiles vectorielles gnres ct client.
 * 
 * @category Core
 * @group Components
 */
export class StreamingMap extends HTMLElement {
  private map: maplibregl.Map | null = null;
  private isMapLoaded: boolean = false;
  private profiler: PerformanceMonitor | null = null;
  private loader: GeoJSONLoader = new GeoJSONLoader();
  private loadVersion: number = 0;
  private batchCount: number = 0;
  private lastRefreshTime: number = 0;
  private refreshTimeout: ReturnType<typeof setTimeout> | null = null;
  private userInteracted: boolean = false;
  private activeBufferIndex: number = 0;
  private layerManager: LayerManager;
  private interactionManager: InteractionManager | null = null;
  private popupManager: PopupManager | null = null;
  private templatesCache: Map<HTMLElement, string> = new Map();
  private instantiatedControls: maplibregl.IControl[] = [];
  
  constructor() {
    super();
    this.attachShadow({ mode: 'open' });
    this.layerManager = new LayerManager();
  }

  /**
   * Configuration du style des calques (Polygones, Lignes, Points).
   * Applique les changements dynamiquement sans recharger la carte.
   */
  set styleConfig(config: StyleConfig) {
    this.layerManager = new LayerManager(config);
    this.applyStyleConfig();
  }

  private applyStyleConfig() {
    if (this.isMapLoaded && this.map) {
      const config = (this.layerManager as unknown as { config: StyleConfig }).config;
      if (!config) return;
      
      // 1. Mise  jour des calques de donnes streames (Buffers 0 et 1)
      [0, 1].forEach(idx => {
        if (config.fill) {
          Object.entries(config.fill).forEach(([prop, val]) => {
            if (this.map!.getLayer(`data-fill-${idx}`)) this.map!.setPaintProperty(`data-fill-${idx}`, prop, val);
          });
        }
        if (config.line) {
          Object.entries(config.line).forEach(([prop, val]) => {
            if (this.map!.getLayer(`data-line-${idx}`)) this.map!.setPaintProperty(`data-line-${idx}`, prop, val);
          });
        }
        if (config.circle) {
          Object.entries(config.circle).forEach(([prop, val]) => {
            if (this.map!.getLayer(`data-circle-${idx}`)) this.map!.setPaintProperty(`data-circle-${idx}`, prop, val);
          });
        }
      });
    }
  }

  static registerControl(name: string, factory: ControlFactory) {
    globalControlRegistry.register(name, factory);
  }

  static get observedAttributes() {
    return ['src', 'tile-url', 'controls', 'debug'];
  }

  connectedCallback() {
    const isDebug = this.hasAttribute('debug');
    this.profiler = new PerformanceMonitor(isDebug);

    const style = document.createElement('style');
    style.textContent = `
      ${mapLibreCss}
      ${mapboxDrawCss}
      :host { display: block; width: 100%; height: 100%; position: relative; overflow: hidden; }
      #map { width: 100%; height: 100%; }
      .maplibregl-map { height: 100%; width: 100%; }
      
      .ui-layer {
        position: absolute;
        top: 0; left: 0; width: 100%; height: 100%;
        pointer-events: none;
        z-index: 10;
        display: grid;
        grid-template-areas: 
          "tl tc tr"
          "ml .  mr"
          "bl bc br";
        grid-template-columns: auto 1fr auto;
        grid-template-rows: auto 1fr auto;
        padding: 10px;
        box-sizing: border-box;
      }

      .ui-area { display: flex; flex-direction: column; gap: 8px; pointer-events: none; }
      .ui-area > * { pointer-events: auto; }
      
      .top-left { grid-area: tl; align-items: flex-start; }
      .top-center { grid-area: tc; align-items: center; }
      .top-right { grid-area: tr; align-items: flex-end; }
      .bottom-left { grid-area: bl; align-items: flex-start; justify-content: flex-end; }
      .bottom-center { grid-area: bc; align-items: center; justify-content: flex-end; }
      .bottom-right { grid-area: br; align-items: flex-end; justify-content: flex-end; }

      ::slotted(*) { pointer-events: auto; }

      /* --- UNIFIED UI STYLING (Glassmorphism) --- */
      .maplibregl-ctrl-group, .modern-ctrl-group {
        background: rgba(255, 255, 255, 0.85) !important;
        backdrop-filter: blur(12px);
        -webkit-backdrop-filter: blur(12px);
        border: 1px solid rgba(255, 255, 255, 0.5) !important;
        box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1) !important;
        border-radius: 12px !important;
        overflow: hidden;
        display: flex;
        flex-direction: column;
      }
      
      /* Style pour l'empilement horizontal si besoin (ex: top-center) */
      .top-center .modern-ctrl-group, .bottom-center .modern-ctrl-group {
        flex-direction: row;
      }

      .maplibregl-ctrl-group button, .modern-ctrl-group button {
        width: 38px !important;
        height: 38px !important;
        border: none !important;
        border-bottom: 1px solid rgba(0,0,0,0.05) !important;
        background-color: transparent !important;
        font-size: 1.1rem;
        cursor: pointer;
        display: flex;
        align-items: center;
        justify-content: center;
        opacity: 0.8;
        transition: all 0.2s ease;
        color: var(--text-main);
      }
      
      .maplibregl-ctrl-group button:last-child, .modern-ctrl-group button:last-child {
        border-bottom: none !important;
      }

      .maplibregl-ctrl-group button:hover, .modern-ctrl-group button:hover {
        opacity: 1;
        background-color: rgba(255, 255, 255, 0.5) !important;
        transform: scale(1.05);
      }
      
      /* Pour les icnes de zoom natives qui utilisent des images de fond */
      .maplibregl-ctrl-icon {
        background-color: transparent !important;
      }

      /* Reset margins for controls inside our UI grid */
      .maplibregl-ctrl, .maplibregl-ctrl {
        margin: 0 !important;
      }

      /* --- PRINT STYLES --- */
      @media print {
        :host {
          display: block !important;
          width: 100vw !important;
          height: 100vh !important;
        }
        .ui-layer, .perf-monitor, .maplibregl-control-container {
          display: none !important;
        }
        #map, .maplibregl-map, .maplibregl-canvas {
          width: 100% !important;
          height: 100% !important;
          position: absolute !important;
          top: 0 !important;
          left: 0 !important;
        }
      }
    `;
    this.shadowRoot!.appendChild(style);

    const mapContainer = document.createElement('div');
    mapContainer.id = 'map';
    this.shadowRoot!.appendChild(mapContainer);

    // Cration de la grille d'interface
    const uiLayer = document.createElement('div');
    uiLayer.className = 'ui-layer';

    const areas = ['top-left', 'top-center', 'top-right', 'bottom-left', 'bottom-center', 'bottom-right'];
    areas.forEach(area => {
      const div = document.createElement('div');
      div.className = `ui-area ${area}`;
      const slot = document.createElement('slot');
      slot.name = area;
      div.appendChild(slot);
      uiLayer.appendChild(div);
    });

    // Slot par dfaut (pour les overlays libres)
    const defaultSlot = document.createElement('slot');
    uiLayer.appendChild(defaultSlot);

    // Slot cach pour le popup
    const popupSlot = document.createElement('slot');
    popupSlot.name = 'popup';
    popupSlot.style.display = 'none';
    uiLayer.appendChild(popupSlot);

    this.shadowRoot!.appendChild(uiLayer);

    if (this.profiler?.getOverlay()) {
      this.shadowRoot!.appendChild(this.profiler.getOverlay()!);
    }

    this.initMap(mapContainer);
  }

  disconnectedCallback() {
    if (this.interactionManager) {
      this.interactionManager.destroy();
    }
    this.instantiatedControls.forEach(ctrl => {
      if (this.map) ctrl.onRemove(this.map);
    });
    if (this.map) {
      this.map.remove();
    }
  }

  attributeChangedCallback(name: string, oldValue: string, newValue: string) {
    if (oldValue === newValue) return;
    if (name === 'src' && this.isMapLoaded) {
      this.loadGeoJSONStream(newValue);
    }
    if (name === 'controls' && this.map) {
      this.updateControls(newValue);
    }
    if (name === 'tile-url' && this.map) {
      this.swapBaseLayer(newValue);
    }
    if (name === 'debug') {
      const isDebug = this.hasAttribute('debug');
      if (isDebug) {
        if (!this.profiler?.getOverlay()) {
          this.profiler = new PerformanceMonitor(true);
          if (this.shadowRoot && this.profiler.getOverlay()) {
            this.shadowRoot.appendChild(this.profiler.getOverlay()!);
          }
        }
      } else {
        if (this.profiler) {
          const overlay = this.profiler.getOverlay();
          if (overlay?.parentNode) overlay.parentNode.removeChild(overlay);
          this.profiler = null;
        }
      }
    }
  }
  
  public registerControl(name: string, factory: ControlFactory) {
    globalControlRegistry.register(name, factory);
  }

  /**
   * Charge un objet GeoJSON directement dans la carte sans passer par une URL externe.
   * @param geojson Objet GeoJSON (Feature ou FeatureCollection)
   */
  /**
   * Charge un objet GeoJSON directement dans la carte.
   * @param geojson Objet GeoJSON
   * @param append Si true, ajoute les donnes aux existantes au lieu de les remplacer.
   */
  public async loadData(geojson: unknown, append: boolean = false) {
    const blob = new Blob([JSON.stringify(geojson)], { type: 'application/json' });
    const url = URL.createObjectURL(blob);
    await this.loadGeoJSONStream(url, !append);
    setTimeout(() => URL.revokeObjectURL(url), 5000);
  }

  /**
   * Ajoute plusieurs points en une seule fois.
   * @param points Tableau d'objets {lng, lat, properties}
   * @param append Si true, conserve les points dj prsents sur la carte.
   */
  public addPoints(points: {lng: number, lat: number, properties?: object}[], append: boolean = false) {
    this.loadData({
      type: 'FeatureCollection',
      features: points.map(p => ({
        type: 'Feature',
        geometry: { type: 'Point', coordinates: [p.lng, p.lat] },
        properties: p.properties || {}
      }))
    }, append);
  }

  /**
   * Ajoute un point unique  la carte de manire simplifie.
   * @param lng Longitude
   * @param lat Latitude
   * @param properties Objet contenant les mtadonnes (optionnel)
   * @param append Si true, ne supprime pas les donnes existantes (dfaut: false).
   */
  public addPoint(lng: number, lat: number, properties: object = {}, append: boolean = false) {
    this.loadData({
      type: 'Feature',
      geometry: { type: 'Point', coordinates: [lng, lat] },
      properties: properties
    }, append);
  }

  private initMap(container: HTMLElement) {
    const defaultTileUrl = this.getAttribute('tile-url') || 'https://tile.openstreetmap.org/{z}/{x}/{y}.png';

    maplibregl.addProtocol('streaming', async (params: RequestParameters) => {
      const start = performance.now();
      // On ignore tout paramtre de version dans l'URL pour l'extraction des coordonnes
      const urlWithoutQuery = params.url.split('?')[0];
      const parts = urlWithoutQuery.split('://')[1].split('/');
      const z = parseInt(parts[0]);
      const x = parseInt(parts[1]);
      const y = parseInt(parts[2].split('.')[0]);

      const data = await this.loader.getTile(z, x, y);
      
      const duration = performance.now() - start;
      globalPerformanceCollector.logMetric('Protocol Latency', duration, 'ms');

      // On retourne un buffer binaire
      return { data: data || new Uint8Array() };
    });

    this.map = new maplibregl.Map({
      container: container,
      style: {
        version: 8,
        sources: {
          'raster-tiles': {
            type: 'raster',
            tiles: [defaultTileUrl],
            tileSize: 256,
            attribution: ' OpenStreetMap contributors'
          },
          'streamed-data-0': {
            type: 'vector',
            tiles: ['streaming://{z}/{x}/{y}.pbf'],
            maxzoom: 14
          },
          'streamed-data-1': {
            type: 'vector',
            tiles: ['streaming://{z}/{x}/{y}.pbf'],
            maxzoom: 14
          }
        },
        layers: [
          { id: 'simple-tiles', type: 'raster', source: 'raster-tiles', minzoom: 0, maxzoom: 22 },
          ...this.layerManager.generateLayers()
        ]
      },
      center: [2.35, 48.85],
      zoom: 5
    });

    const controlsAttr = this.getAttribute('controls') || 'zoom:top-right, layers:top-right';
    this.updateControls(controlsAttr);

    this.map.on('load', () => {
      this.isMapLoaded = true;
      
      // R-appliquer le style au cas o il a t dfini avant le chargement complet
      this.applyStyleConfig();

      this.popupManager = new PopupManager(this.map!, maplibregl);

      // Initialize Interaction Manager once map is loaded
      this.interactionManager = new InteractionManager(this.map!, (event: InteractionEvent) => {
        // 1. Dispatch Custom Event
        this.dispatchEvent(new CustomEvent('feature-click', { 
          detail: { 
            feature: event.feature, 
            originalEvent: event.originalEvent 
          } 
        }));

        // 2. Data Binding for Slotted Elements (Overlay UI)
        if (event.type === 'click') {
          const props = event.feature.properties || {};
          
          // Find all elements in slots that want data binding OR visibility toggle
          const boundElements = Array.from(this.querySelectorAll('[data-bind-selection], [hidden-until-selection]')) as HTMLElement[];
          
          boundElements.forEach(el => {
            // Cache original template if not present
            if (!this.templatesCache.has(el)) {
              this.templatesCache.set(el, el.innerHTML);
            }
            
            // Render content
            const template = this.templatesCache.get(el)!;
            // Simple regex replacement
            el.innerHTML = template.replace(/\{\{(\w+)\}\}/g, (_, key) => {
              return props[key] !== undefined ? String(props[key]) : '';
            });
            
            // Handle visibility
            if (el.hasAttribute('hidden-until-selection')) {
              el.removeAttribute('hidden');
              el.style.display = ''; // Reset display if it was hidden via style
            }
          });
        }

        // 3. Handle Popup if template exists
        const popupSlot = this.shadowRoot!.querySelector('slot[name="popup"]') as HTMLSlotElement;
        if (popupSlot && event.type === 'click') {
          const elements = popupSlot.assignedNodes();
          if (elements.length > 0 && elements[0] instanceof HTMLElement) {
             // Use innerHTML of the template element
             // Note: User should provide <template slot="popup">...</template>
             // But assignedNodes returns the template element itself if valid.
             // If user uses <div slot="popup">, we use outerHTML.
             // Best practice: <template>.
             let templateContent = '';
             const node = elements[0];
             if (node instanceof HTMLTemplateElement) {
               templateContent = node.innerHTML;
             } else if (node instanceof HTMLElement) {
               templateContent = node.outerHTML;
             }
             
             if (templateContent) {
               this.popupManager?.showPopup(event.feature, templateContent, event.lngLat);
             }
          }
        }
      });

      const src = this.getAttribute('src');
      if (src) this.loadGeoJSONStream(src);
    });

    // Detect user interaction to stop auto-fitting
    const interactionEvents = ['mousedown', 'touchstart', 'wheel', 'boxzoomstart', 'dragstart'];
    interactionEvents.forEach(event => {
      this.map!.on(event, () => {
        this.userInteracted = true;
      });
    });
  }

  private updateControls(configString: string) {
    // 1. Nettoyage des contrles existants
    this.instantiatedControls.forEach(ctrl => {
      if (this.map) ctrl.onRemove(this.map);
    });
    this.instantiatedControls = [];
    
    // Nettoyage visuel des slots dans le Shadow DOM
    const areas = ['top-left', 'top-center', 'top-right', 'bottom-left', 'bottom-center', 'bottom-right'];
    areas.forEach(area => {
      const container = this.shadowRoot!.querySelector(`.${area}`);
      if (container) container.innerHTML = '';
    });

    const configs = configString.split(',').filter(s => s.trim() !== '').map(s => s.trim());
    configs.forEach(async conf => {
      const [name, position] = conf.split(':').map(s => s.trim());
      const controlOrPromise = globalControlRegistry.create(name);
      
      if (controlOrPromise) {
        // Attente du contrle (qu'il soit synchrone ou asynchrone)
        const control = await Promise.resolve(controlOrPromise);
        
        // eslint-disable-next-line @typescript-eslint/no-explicit-any
        if ('setOnChange' in control && typeof (control as any).setOnChange === 'function') {
           // eslint-disable-next-line @typescript-eslint/no-explicit-any
           (control as any).setOnChange((layer: any) => this.swapBaseLayer(layer.url));
        }
        
        const el = control.onAdd(this.map!);
        this.instantiatedControls.push(control);

        // Trouver la zone correspondante dans le Shadow DOM
        const targetArea = position || 'top-right';
        const container = this.shadowRoot!.querySelector(`.${targetArea}`);
        if (container) {
          const wrapper = document.createElement('div');
          wrapper.className = 'mapboxgl-ctrl';
          wrapper.appendChild(el);
          container.appendChild(wrapper);
        }
      }
    });
  }

  private swapBaseLayer(newUrl: string) {
    if (!this.map) return;
    const sourceId = 'raster-tiles';
    const layerId = 'simple-tiles';
    if (this.map.getLayer(layerId)) this.map.removeLayer(layerId);
    if (this.map.getSource(sourceId)) this.map.removeSource(sourceId);
    this.map.addSource(sourceId, { type: 'raster', tiles: [newUrl], tileSize: 256, attribution: ' OpenStreetMap contributors' });
    const firstDataLayerId = 'data-fill-0'; 
    this.map.addLayer({ id: layerId, type: 'raster', source: sourceId, minzoom: 0, maxzoom: 22 }, this.map.getLayer(firstDataLayerId) ? firstDataLayerId : undefined);
  }

  private async loadGeoJSONStream(url: string, clear: boolean = true) {
    this.dispatchEvent(new CustomEvent('load-start', { detail: { url } }));

    // Define setVisibility in the method scope so it's accessible everywhere
    const setVisibility = (idx: number, visible: boolean) => {
      ['data-fill', 'data-line', 'data-circle'].forEach(prefix => {
        const id = `${prefix}-${idx}`;
        if (this.map?.getLayer(id)) {
          this.map.setLayoutProperty(id, 'visibility', visible ? 'visible' : 'none');
        }
      });
    };

    if (clear) {
      this.loadVersion = Date.now();
      this.batchCount = 0;
      this.userInteracted = false; 
      this.activeBufferIndex = 0;
      
      // Clear any active popup
      this.popupManager?.clearPopup();
      
      // Reset both sources
      ['streamed-data-0', 'streamed-data-1'].forEach(id => {
        const source = this.map?.getSource(id) as maplibregl.VectorTileSource;
        if (source) source.setTiles([`streaming://{z}/{x}/{y}.pbf?v=${this.loadVersion}.0`]);
      });
      
      // Reset visibility: Hide BOTH to force a clear ("jump") indicating change
      setVisibility(0, false);
      setVisibility(1, false);
    }

    try {
      await this.loader.load(
        url,
        (bounds: number[][]) => {
          this.batchCount++;
          
          if (!this.userInteracted && bounds[0][0] !== 180 && clear) {
             const duration = this.batchCount === 1 ? 1000 : 500;
             this.map?.fitBounds(bounds as maplibregl.LngLatBoundsLike, { padding: 50, duration });
          }

          const now = Date.now();
          const timeSinceLast = now - this.lastRefreshTime;
          if (this.refreshTimeout) clearTimeout(this.refreshTimeout);

          const performRefresh = () => {
            this.profiler?.start('Refresh Tiles');
            
            // DOUBLE BUFFERING STRATEGY
            const nextIndex = this.activeBufferIndex === 0 ? 1 : 0;
            const nextSourceId = `streamed-data-${nextIndex}`;
            const source = this.map?.getSource(nextSourceId) as maplibregl.VectorTileSource;
            
            if (source) {
              // 2. Point next source to new data version
              source.setTiles([`streaming://{z}/{x}/{y}.pbf?v=${this.loadVersion}.${this.batchCount}`]);
              
              // 3. Swap Visibility SECURELY
              const swap = () => {
                setVisibility(nextIndex, true);
                
                // CRITICAL FIX: Delay hiding the old buffer to ensure the new one is painted.
                // Immediate hiding causes flickering if the new layer isn't fully composited yet.
                // 250ms provides a safe overlap (superposition state).
                setTimeout(() => {
                  setVisibility(this.activeBufferIndex, false);
                  this.activeBufferIndex = nextIndex;
                  this.profiler?.end('Refresh Tiles');
                }, 250);
              };

              // Listener to detect when the new source is ready
              // eslint-disable-next-line @typescript-eslint/no-explicit-any
              const onSourceData = (e: any) => {
                // Check if this source is fully loaded or if a specific tile update finished
                // Removing 'e.tile' check as it might be too restrictive for general source update
                if (e.sourceId === nextSourceId && e.isSourceLoaded) {
                   this.map?.off('sourcedata', onSourceData);
                   if (timeoutId) clearTimeout(timeoutId);
                   swap();
                }
              };
              
              this.map?.on('sourcedata', onSourceData);
              
              // Safety fallback
              const timeoutId = setTimeout(() => {
                this.map?.off('sourcedata', onSourceData);
                swap();
              }, 300); 
            }
            
            this.lastRefreshTime = Date.now();
            this.refreshTimeout = null;
          };

          if (timeSinceLast > 500) {
            performRefresh();
          } else {
            this.refreshTimeout = setTimeout(performRefresh, 500 - timeSinceLast);
          }
        },
        (bytes: number, features: number) => {
          this.profiler?.logValue('Bytes Received', bytes / 1024, 'KB');
          this.profiler?.logValue('Total Features', features, 'f');
          this.dispatchEvent(new CustomEvent('load-progress', { detail: { bytes, features } }));
        }
      );
      this.dispatchEvent(new CustomEvent('load-end', { detail: {} }));
    } catch (err: unknown) {
      this.dispatchEvent(new CustomEvent('load-error', { detail: { error: (err as Error).message } }));
      console.error("Streaming error:", err);
    }
  }
}
