import type { Map, MapGeoJSONFeature, LngLatLike, Popup } from 'maplibre-gl';

// Interface partielle pour mocker maplibregl
interface MapLibreNamespace {
  Popup: new (options?: { closeButton?: boolean, closeOnClick?: boolean }) => Popup;
}

export class PopupManager {
  private map: Map;
  private maplibregl: MapLibreNamespace;
  private currentPopup: Popup | null = null;

  constructor(map: Map, maplibreglInstance: MapLibreNamespace) {
    this.map = map;
    this.maplibregl = maplibreglInstance;
  }

  public showPopup(feature: MapGeoJSONFeature, template: string, location?: LngLatLike) {
    if (this.currentPopup) {
      this.currentPopup.remove();
    }

    const coordinates = location || this.getPopupLocation(feature);
    const content = this.renderTemplate(template, feature.properties || {});

    this.currentPopup = new this.maplibregl.Popup({ closeButton: true, closeOnClick: true })
      .setLngLat(coordinates)
      .setHTML(content)
      .addTo(this.map);
  }

  public clearPopup() {
    if (this.currentPopup) {
      this.currentPopup.remove();
      this.currentPopup = null;
    }
  }

  private getPopupLocation(feature: MapGeoJSONFeature): LngLatLike {
    const geom = feature.geometry;
    
    // Helper to calculate center of bounds
    const getBoundsCenter = (coords: number[][]): LngLatLike => {
      let minLng = 180, minLat = 90, maxLng = -180, maxLat = -90;
      coords.forEach(pt => {
        const [lng, lat] = pt;
        if (lng < minLng) minLng = lng;
        if (lng > maxLng) maxLng = lng;
        if (lat < minLat) minLat = lat;
        if (lat > maxLat) maxLat = lat;
      });
      return [(minLng + maxLng) / 2, (minLat + maxLat) / 2];
    };

    if (geom.type === 'Point') {
      return geom.coordinates as LngLatLike;
    } else if (geom.type === 'Polygon') {
      // Use outer ring
      return getBoundsCenter(geom.coordinates[0] as number[][]);
    } else if (geom.type === 'MultiPolygon') {
      // Use first polygon's outer ring (simplification)
      return getBoundsCenter((geom.coordinates[0] as number[][][])[0]);
    } else if (geom.type === 'LineString') {
      return getBoundsCenter(geom.coordinates as number[][]);
    } else if (geom.type === 'MultiLineString') {
      return getBoundsCenter((geom.coordinates as number[][][])[0]);
    }
    
    return [0, 0]; // Fallback
  }

  private renderTemplate(template: string, properties: Record<string, string | number | boolean | null>): string {
    if (!properties) return template;
    
    // Remplacement basique {{key}}
    return template.replace(/\{\{(\w+)\}\}/g, (_, key) => {
      return properties[key] !== undefined ? String(properties[key]) : '';
    });
  }
}
