import { StreamingMap } from './StreamingMap';
import styleText from './style.css?inline';

// Injection du style global dans le HEAD (pour qu'il soit inclus dans le JS)
const styleEl = document.createElement('style');
styleEl.textContent = styleText;
document.head.appendChild(styleEl);

// Exemple de cration d'un Plugin Custom via l'API JS
class ResetViewControl {
  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  private map: any;
  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  onAdd(map: any) {
    this.map = map;
    const btn = document.createElement('button');
    btn.className = 'maplibregl-ctrl maplibregl-ctrl-group';
    btn.innerHTML = '<span style="font-size:1.2em;"></span>';
    btn.title = "Rinitialiser la vue";
    btn.style.cursor = 'pointer';
    btn.style.width = '29px';
    btn.style.height = '29px';
    btn.style.border = 'none';
    btn.style.background = 'white';
    
    btn.onclick = () => {
      this.map.flyTo({ center: [2.35, 48.85], zoom: 5 });
    };
    return btn;
  }
  onRemove() { this.map = undefined; }
}

// 1. Enregistrement du plugin
StreamingMap.registerControl('reset', () => new ResetViewControl());

// 2. Dfinition du Web Component
if (!customElements.get('streaming-map')) {
  customElements.define('streaming-map', StreamingMap);
}

console.log('Main loaded & Component defined');

// Initialisation SW
if ('serviceWorker' in navigator) {
  window.addEventListener('load', () => {
    navigator.serviceWorker.register('/app-sw.js').catch(err => console.log('SW Error', err));
  });
}

// Interactions UI
document.addEventListener('DOMContentLoaded', () => {
  const mapElement = document.getElementById('main-map') as HTMLElement;
  const select = document.getElementById('source-select') as HTMLSelectElement;
  const debugToggle = document.getElementById('debug-toggle') as HTMLInputElement;
  const loadInfo = document.getElementById('load-info') as HTMLElement;

  if (!mapElement) return;

  // Ecoute des vnements
  mapElement.addEventListener('load-start', ((e: CustomEvent) => {
    loadInfo.textContent = `Chargement de ${e.detail.url.split('/').pop()}...`;
    loadInfo.style.color = '#f1c40f';
  }) as EventListener);

  mapElement.addEventListener('load-progress', ((e: CustomEvent) => {
    const { bytes, features } = e.detail;
    loadInfo.textContent = `Tlchargement : ${(bytes / 1024 / 1024).toFixed(2)} Mo (${features} entits)`;
  }) as EventListener);

  mapElement.addEventListener('load-end', ((e: CustomEvent) => {
    loadInfo.textContent = `Termin : ${e.detail.total || ''} entits charges.`;
    loadInfo.style.color = '#27ae60';
  }) as EventListener);

  // NOUVEAU : coute des clics sur les features (Interactivit)
  mapElement.addEventListener('feature-click', ((e: CustomEvent) => {
    const { feature } = e.detail;
    console.log('Entit clique :', feature.properties);
    // On pourrait dclencher une action externe ici
  }) as EventListener);

  if (select) {
    select.addEventListener('change', (e: Event) => {
      const target = e.target as HTMLSelectElement;
      
      // EXEMPLE : Personnalisation dynamique du style selon la source
      const isEarthquake = target.value.includes('earthquake');
      const streamingMap = mapElement as StreamingMap;
      streamingMap.styleConfig = {
        circle: { 
          'circle-color': isEarthquake ? '#ff5722' : '#27ae60',
          'circle-radius': isEarthquake ? 8 : 4,
          'circle-stroke-width': 1,
          'circle-stroke-color': '#fff'
        },
        fill: {
          'fill-color': isEarthquake ? '#ff5722' : '#3498db',
          'fill-opacity': 0.3
        }
      };

      mapElement.setAttribute('src', target.value);
    });
  }

  if (debugToggle) {
    debugToggle.addEventListener('change', (e: Event) => {
      const target = e.target as HTMLInputElement;
      if (target.checked) {
        mapElement.setAttribute('debug', '');
      } else {
        mapElement.removeAttribute('debug');
      }
    });
    if (mapElement.hasAttribute('debug')) debugToggle.checked = true;
  }
});
